'use client'

import { PublicKey } from '@solana/web3.js'
import { useMemo, useState, useEffect } from 'react'
import { ExplorerLink } from '../cluster/cluster-ui'
import { useCoinFlipProgram, useCoinFlipProgramAccount } from './coin-flip-data-access'
import { ellipsify } from '@/lib/utils'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '../ui/card'
import { useWallet } from '@solana/wallet-adapter-react'
import { Coins, Settings, DollarSign } from 'lucide-react'
import BN from 'bn.js'

const LAMPORTS_PER_SOL = 1_000_000_000

export function CoinFlipCreate() {
  const { initialize, accounts } = useCoinFlipProgram()
  const wallet = useWallet()
  const [feePercentage, setFeePercentage] = useState(5)

  const existingGameState = useMemo(() => {
    if (!wallet.publicKey || !accounts.data) return null
    return accounts.data.find((account: any) => account.account.owner.equals(wallet.publicKey!))
  }, [accounts.data, wallet.publicKey])

  const handleInitialize = () => {
    if (!wallet.publicKey || existingGameState) return
    initialize.mutateAsync({ owner: wallet.publicKey, fee_percentage: feePercentage })
  }

  return (
    <Card className="mb-6">
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <Settings className="h-5 w-5" />
          Create Game State
        </CardTitle>
        <CardDescription>Initialize a new game state to start the coin flip game</CardDescription>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          <div className="flex items-center gap-4">
            <label className="text-sm font-medium text-gray-700">House Fee:</label>
            <input
              type="number"
              min={0}
              max={100}
              value={feePercentage}
              onChange={(e) => setFeePercentage(Number(e.target.value))}
              className="w-20 px-3 py-2 border rounded-md"
            />
            <span className="text-sm">%</span>
          </div>
          
          <Button 
            onClick={handleInitialize} 
            disabled={initialize.isPending || !!existingGameState}
            className="w-full"
          >
            {initialize.isPending ? 'Initializing...' : 
             existingGameState ? 'Game State Already Exists' : 
             'Initialize Game State'}
          </Button>
        </div>
      </CardContent>
    </Card>
  )
}

export function CoinFlipList() {
  const { accounts, getProgramAccount } = useCoinFlipProgram()

  if (getProgramAccount.isLoading) {
    return <div className="flex justify-center py-8">Loading...</div>
  }
  
  if (!getProgramAccount.data?.value) {
    return (
      <Card>
        <CardContent className="pt-6">
          <div className="text-center py-8">
            <p>Program account not found. Make sure you have deployed the program.</p>
          </div>
        </CardContent>
      </Card>
    )
  }

  return (
    <div className="space-y-6">
      {accounts.isLoading ? (
        <div className="flex justify-center py-8">Loading game states...</div>
      ) : accounts.data?.length ? (
        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
          {accounts.data?.map((account: any) => (
            <CoinFlipCard key={account.publicKey.toString()} gameStatePubkey={account.publicKey} />
          ))}
        </div>
      ) : (
        <Card>
          <CardContent className="pt-6">
            <div className="text-center py-8">
              <h2 className="text-xl font-bold mb-2">No Game States</h2>
              <p>Create one above to get started!</p>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  )
}

function CoinFlipCard({ gameStatePubkey }: { gameStatePubkey: PublicKey }) {
  const wallet = useWallet()
  const { deposit_to_gamestate, withdraw, accounts, program } = useCoinFlipProgram()
  
  const [vaultAmount, setVaultAmount] = useState<number>(0)
  const [betChoice, setBetChoice] = useState<number>(0)
  const [placeBetAmount, setPlaceBetAmount] = useState<number>(0)
  const [betResult, setBetResult] = useState<null | 'won' | 'lost' | 'pending'>(null)

  const gameStateData = useMemo(() => {
    return accounts.data?.find((account: any) => account.publicKey.equals(gameStatePubkey))
  }, [accounts.data, gameStatePubkey])

  const betAccountQuery = useCoinFlipProgramAccount({
    user: wallet.publicKey || new PublicKey('11111111111111111111111111111111'),
    owner: gameStateData?.account?.owner || new PublicKey('11111111111111111111111111111111'),
    gameNumber: gameStateData?.account?.totalGames?.toNumber()
  })

  const isOwner = wallet.publicKey && gameStateData?.account?.owner.equals(wallet.publicKey)

  const potentialWinnings = useMemo(() => {
    if (!placeBetAmount || !gameStateData?.account) return 0
    const payout = placeBetAmount * 2
    const fee = (payout * gameStateData.account.feePercentage.toNumber()) / 100
    return payout - fee
  }, [placeBetAmount, gameStateData?.account])

  const existingBetInfo = useMemo(() => {
    const betData = betAccountQuery.accountQuery.data
    if (!betData?.settled) return null
    
    return {
      amount: betData.amount.toNumber() / LAMPORTS_PER_SOL,
      choice: betData.choice === 0 ? 'Heads' : 'Tails',
      won: betData.won
    }
  }, [betAccountQuery.accountQuery.data])

  // Reset form values after successful operations
  useEffect(() => {
    if (deposit_to_gamestate.isSuccess || withdraw.isSuccess) {
      setVaultAmount(0)
    }
  }, [deposit_to_gamestate.isSuccess, withdraw.isSuccess])

  useEffect(() => {
    if (betAccountQuery.placeBet.isPending) {
      setBetResult('pending')
    }
  }, [betAccountQuery.placeBet.isPending])

  const handleVaultOperation = async (operation: 'deposit' | 'withdraw') => {
    if (!wallet.publicKey || !vaultAmount || vaultAmount <= 0) return

    const amountLamports = vaultAmount * LAMPORTS_PER_SOL
    
    try {
      if (operation === 'deposit') {
        await deposit_to_gamestate.mutateAsync({ owner: wallet.publicKey, amount: amountLamports })
      } else {
        await withdraw.mutateAsync({ owner: wallet.publicKey, amount: amountLamports })
      }
    } catch (error) {
      console.error(`${operation} failed:`, error)
    }
  }

  const checkBetResult = async (gameNumber: number) => {
    if (!wallet.publicKey) return

    const gameNumberBuf = new BN(gameNumber).toArrayLike(Buffer, 'le', 8)
    const [correctBetAccount] = PublicKey.findProgramAddressSync(
      [Buffer.from('bet_account'), wallet.publicKey.toBuffer(), gameNumberBuf],
      (program as any).programId
    )

    let attempts = 0
    const maxAttempts = 10

    const attemptFetch = async () => {
      attempts++
      
      try {
        const betData = await (program.account as any).betAccount.fetch(correctBetAccount)
        
        if (betData?.settled) {
          setBetResult(betData.won ? 'won' : 'lost')
          setPlaceBetAmount(0)
          accounts.refetch()
          return
        }
        
        if (attempts < maxAttempts) {
          setTimeout(attemptFetch, 2000)
        } else {
          setBetResult(null)
          setPlaceBetAmount(0)
        }
      } catch (error) {
        if (attempts < maxAttempts) {
          setTimeout(attemptFetch, 2000)
        } else {
          setBetResult(null)
          setPlaceBetAmount(0)
        }
      }
    }

    setTimeout(attemptFetch, 3000)
  }

  const handlePlaceBet = async () => {
    if (!wallet.publicKey || !gameStateData?.account?.owner || !placeBetAmount || placeBetAmount <= 0) {
      return
    }

    setBetResult('pending')
    const amountLamports = placeBetAmount * LAMPORTS_PER_SOL

    try {
      const gameNumberForBet = betAccountQuery.currentGameNumber
      
      await betAccountQuery.placeBet.mutateAsync({
        amount: amountLamports,
        choice: betChoice
      })
      
      checkBetResult(gameNumberForBet)
    } catch (error) {
      console.error("Bet failed:", error)
      setBetResult(null)
    }
  }

  if (!gameStateData?.account) {
    return <div>Loading game state...</div>
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <Coins className="h-5 w-5" />
          Game State
        </CardTitle>
        <CardDescription>
          <ExplorerLink 
            path={`account/${gameStatePubkey}`} 
            label={ellipsify(gameStatePubkey.toString())}
          />
        </CardDescription>
      </CardHeader>
      
      <CardContent className="space-y-4">
        {/* Game State Info */}
        <div className="bg-blue-50 rounded-lg p-4 border">
          <div className="grid grid-cols-2 gap-4 text-sm">
            <div>
              <span className="font-medium text-blue-700">Total Games:</span>
              <p className="font-semibold text-blue-900">{gameStateData.account.totalGames.toString()}</p>
            </div>
            <div>
              <span className="font-medium text-blue-700">Vault Balance:</span>
              <p className="font-semibold text-blue-900">
                {(gameStateData.account.totalAmount.toNumber() / LAMPORTS_PER_SOL).toFixed(4)} SOL
              </p>
            </div>
            <div>
              <span className="font-medium text-blue-700">House Fee:</span>
              <p className="font-semibold text-blue-900">{gameStateData.account.feePercentage.toString()}%</p>
            </div>
            <div>
              <span className="font-medium text-blue-700">Owner:</span>
              <p className="font-semibold text-blue-900">{ellipsify(gameStateData.account.owner.toString())}</p>
            </div>
          </div>
        </div>

        {/* Previous Bet Result */}
        {existingBetInfo && (
          <div className={`rounded-lg p-4 border ${
            existingBetInfo.won ? 'bg-green-50 border-green-200' : 'bg-red-50 border-red-200'
          }`}>
            <h4 className="font-semibold mb-2">Your Last Bet</h4>
            <div className="text-sm space-y-1">
              <p><span className="font-medium">Amount:</span> {existingBetInfo.amount.toFixed(4)} SOL</p>
              <p><span className="font-medium">Choice:</span> {existingBetInfo.choice}</p>
              <p className={`font-semibold ${existingBetInfo.won ? 'text-green-600' : 'text-red-600'}`}>
                {existingBetInfo.won ? ' You Won!' : ' You Lost'}
              </p>
            </div>
          </div>
        )}

        {/* Vault Operations - Owner Only */}
        {isOwner && (
          <div className="bg-white rounded-lg p-4 border">
            <h4 className="font-semibold mb-3 flex items-center gap-2">
              <DollarSign className="h-4 w-4 text-green-600" />
              Vault Operations
            </h4>
            
            <div className="space-y-3">
              <div className="flex items-center gap-2">
                <input
                  type="number"
                  min={0}
                  step={0.1}
                  placeholder="Amount in SOL"
                  value={vaultAmount || ''}
                  onChange={(e) => setVaultAmount(Number(e.target.value))}
                  disabled={deposit_to_gamestate.isPending || withdraw.isPending}
                  className="flex-1 px-3 py-2 border rounded-md text-gray-900"
                />
                <span className="text-sm text-gray-700">SOL</span>
              </div>
              
              <div className="flex gap-2">
                <Button 
                  onClick={() => handleVaultOperation('deposit')} 
                  disabled={deposit_to_gamestate.isPending || withdraw.isPending || !vaultAmount}
                  className="flex-1"
                >
                  {deposit_to_gamestate.isPending ? 'Depositing...' : 'Deposit'}
                </Button>
                
                <Button 
                  onClick={() => handleVaultOperation('withdraw')} 
                  disabled={withdraw.isPending || deposit_to_gamestate.isPending || !vaultAmount}
                  variant="destructive" 
                  className="flex-1"
                >
                  {withdraw.isPending ? 'Withdrawing...' : 'Withdraw'}
                </Button>
              </div>
            </div>
          </div>
        )}

        {/* Betting Section */}
        <div className="bg-purple-50 rounded-lg p-4 border">
          <h4 className="font-semibold mb-3 flex items-center gap-2">
            <Coins className="h-4 w-4 text-purple-600" />
            Place Your Bet
          </h4>
          
          <div className="space-y-4">
            <div className="flex items-center gap-2">
              <input
                type="number"
                min={0}
                step={0.1}
                placeholder="Bet amount in SOL"
                value={placeBetAmount || ''}
                onChange={(e) => setPlaceBetAmount(Number(e.target.value))}
                disabled={betAccountQuery.placeBet.isPending}
                className="flex-1 px-3 py-2 border rounded-md text-gray-900"
              />
              <span className="text-sm text-gray-700">SOL</span>
            </div>

            {/* Potential Winnings */}
            {placeBetAmount > 0 && (
              <div className="p-3 bg-yellow-50 rounded-lg border border-yellow-200">
                <p className="text-sm text-yellow-800">
                  <span className="font-medium">Potential Winnings:</span> {potentialWinnings.toFixed(4)} SOL
                </p>
                <p className="text-xs text-yellow-700 mt-1">
                  2x bet amount minus {gameStateData.account.feePercentage.toString()}% house fee
                </p>
              </div>
            )}

            {/* Choice Buttons */}
            <div className="flex gap-2">
              <Button
                variant={betChoice === 0 ? 'default' : 'outline'}
                onClick={() => setBetChoice(0)}
                disabled={betAccountQuery.placeBet.isPending}
                className={`flex-1 ${betChoice === 0 ? 'bg-orange-500 hover:bg-orange-600 text-white' : 'border-orange-500 text-orange-500 hover:bg-orange-50'}`}
              >
                 Heads
              </Button>
              <Button
                variant={betChoice === 1 ? 'default' : 'outline'}
                onClick={() => setBetChoice(1)}
                disabled={betAccountQuery.placeBet.isPending}
                className={`flex-1 ${betChoice === 1 ? 'bg-blue-500 hover:bg-blue-600 text-white' : 'border-blue-500 text-blue-500 hover:bg-blue-50'}`}
              >
                 Tails
              </Button>
            </div>

            {/* Place Bet Button */}
            <Button
              onClick={handlePlaceBet}
              disabled={betAccountQuery.placeBet.isPending || !placeBetAmount || placeBetAmount <= 0 || !wallet.publicKey}
              className="w-full bg-gradient-to-r from-green-500 to-emerald-500 hover:from-green-600 hover:to-emerald-600 text-white font-semibold"
            >
              {betAccountQuery.placeBet.isPending ? 'Flipping Coin...' : ' Flip the Coin!'}
            </Button>
            
            {/* Bet Result */}
            {betResult === 'won' && (
              <div className="text-center p-3 bg-green-50 rounded-lg border border-green-200">
                <p className="text-green-600 font-semibold"> You won the bet!</p>
              </div>
            )}
            {betResult === 'lost' && (
              <div className="text-center p-3 bg-red-50 rounded-lg border border-red-200">
                <p className="text-red-600 font-semibold"> You lost the bet!</p>
              </div>
            )}
            {betResult === 'pending' && (
              <div className="text-center p-3 bg-yellow-50 rounded-lg border border-yellow-200">
                <p className="text-yellow-600 font-medium"> Processing your bet...</p>
              </div>
            )}
          </div>
        </div>
      </CardContent>
    </Card>
  )
}

export default function CoinFlip() {
  return (
    <div className="max-w-7xl mx-auto px-4 py-8 space-y-6">
      <CoinFlipCreate />
      <CoinFlipList />
    </div>
  )
}